const counter = (filters) => (
  filters.reduce((acc, f, index) => {
    if (acc[f.category]) return {...acc, [f.category]: [...acc[f.category], index]};
    else return {...acc, [f.category]: [index] }
  }, {})
);

const filterString = (name, column, type) => {
  const TYPES = {
    select: `Find(%27${name}%27, ${column})`,
    text: `{${column}} = %27${name}%27`
  };

  return TYPES[type];
};

const buildOrQueries = (counterMapping, filters) => {
  let totalQueries = [];
  Object.keys(counterMapping).map(key => {
    const orQueries = counterMapping[key].map(index => {
      const { name, airtableColumn, columnType } = filters[index];
      return filterString(name, airtableColumn, columnType);
    });

    totalQueries.push(`OR(${orQueries.join(', ')})`);
  });

  if (totalQueries.length > 1) {
    totalQueries = `AND(${totalQueries.join(', ')})`;
  }

  return totalQueries;
};

export const buildFilters = (filters) => {
  if (filters.length < 1) return '';

  let filterFormula = '';
  const counterMapping = counter(filters);

  filterFormula = buildOrQueries(counterMapping, filters);

  return `?filterByFormula=${filterFormula}`;
};

export const buildPagination = (pageSize, offset, filterExists) => {
  const joiner = filterExists ? '&' : '?';

  if (offset === "") return `${joiner}pageSize=${pageSize}`;

  return `${joiner}pageSize=${pageSize}&offset=${offset}`;
};

export const debounce = (fn, time) => {
  let timeout;

  return function() {
    const functionCall = () => fn.apply(this, arguments);

    clearTimeout(timeout);
    timeout = setTimeout(functionCall, time);
  };
};



