import React from 'react';
import axios from 'axios';
import { getRouteProps } from 'react-static';

import { debounce, buildFilters, buildPagination } from '../helpers';


class FilterPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      modalRecordImage: null,
      activeFilters: [],
      records: [],
      offset: ""
    };

    this.filterCallback = this.filterCallback.bind(this);
    this.toggleModal = this.toggleModal.bind(this);
  }

  componentDidMount() {
    this.makeFilterRequest(this.state.activeFilters);

    window.setTimeout(
      () => window.addEventListener('scroll', this.scrollCallback.bind(this)),
      1000
    );
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.scrollCallback.bind(this));
  }

  updateRecords(records, append) {
    const mapRecords = records.map(record => (
      {
        id: record.id,
        thumb: record.fields['Thumb URL'],
        image: record.fields['Media URL']
      }
    ));

    return (
      append ?
        [...this.state.records, ...mapRecords]
      :
        mapRecords
    );
  }

  makeFilterRequest(activeFilters, append = false) {
    const data = { headers: { Authorization: "Bearer keyHXT2ee1ZGTfuyq" } };
    const filters = buildFilters(activeFilters);
    const url = this.props.airtableBase
      + filters
      + buildPagination(15, this.state.offset, filters !== '');

    axios.get(url, data)
      .then(response => {
        this.setState({
          loading: false,
          offset: response.data.offset ? response.data.offset : null,
          records: this.updateRecords(response.data.records, append)
        })
      });
  }

  scrollCallback() {
    if ((window.innerHeight + window.pageYOffset) >= document.body.offsetHeight) {
      if (this.state.offset !== null && !this.state.loading) {
        this.loadMoreRecords();
      }
    }
  }

  loadMoreRecords = debounce(
    () => this.setState({ loading: true }, () => this.makeFilterRequest(this.state.activeFilters, true)),
    200
  );

  filterCallback(category, name, airtableColumn, columnType) {
    const index = this.state.activeFilters.findIndex(activeFilter => activeFilter.name === name);
    let activeFilters = [];

    if (index > -1) {
      activeFilters = [
        ...this.state.activeFilters.slice(0, index),
        ...this.state.activeFilters.slice(index + 1)
      ];
    } else {
      activeFilters =  [
        ...this.state.activeFilters,
        {
          name,
          category,
          airtableColumn,
          columnType
        }
      ];
    }

    this.setState({ activeFilters, offset: '' }, () =>
      this.makeFilterRequest(this.state.activeFilters));
  }

  toggleModal(recordId) {
    if (recordId === null) {
      this.setState({ modalRecordImage: null });
    } else {
      const recordForModal = this.state.records.find(record => record.id === recordId);
      this.setState({ modalRecordImage: recordForModal.image });
    }
  }

  render() {
    return this.props.render({
      ...this.state,
      ...this.props,
      filterCallback: this.filterCallback,
      toggleModal: this.toggleModal
    });
  }
};

export default getRouteProps(FilterPage);



