import React from 'react';
import { NavLink } from 'react-static';

const routes = [
  "Benchmark",
  "Pattern",
  "Empathize",
  "Define"/*
  "Ideate",
  "Explore",
  "Validate",
  "Implement"*/
];

export default ({ location }) => {
  return (
    <div className="pure-g">
     <div className="l-box pure-u-1 pure-u-md-1 pure-u-lg-1">
      <div className="pure-menu">
        <ul className="pure-menu-list process-list">

          {routes.map(routeName => {
            const routePath = routeName === "Benchmark" ? "/" : `/${routeName.toLowerCase()}`;

            return (
              <li
                key={routeName}
                className={routePath === location.pathname ? "pure-menu-item pure-menu-selected" : "pure-menu-item"}
              >
                <NavLink
                  className="pure-menu-link"
                  exact={routeName === "Benchmark"}
                  to={routePath}
                >
                  {routeName}
                </NavLink>
              </li>
            );
          })}

          <li className="pure-menu-item pure-menu-disabled">
            Ideate
          </li>
          <li className="pure-menu-item pure-menu-disabled">
            Explore
          </li>
          <li className="pure-menu-item pure-menu-disabled">
            Validate
          </li>
          <li className="pure-menu-item pure-menu-disabled">
            Implement
          </li>

        </ul>
      </div>
    </div>
  </div>
  );
};



