import React from 'react';
import ReactModal from 'react-modal';

import Filters from './Filters';
import Navbar from './Navbar';
import Records from './Records';


ReactModal.defaultStyles.overlay.backgroundColor = "#142434e8";
ReactModal.defaultStyles.content = { marginTop: "50px" };

const Page = ({ title, description, filters, records, filterCallback, location, toggleModal, modalRecordImage }) => (
  <div>
    <ReactModal
      isOpen={modalRecordImage !== null}
      shouldCloseOnOverlayClick={true}
      onRequestClose={() => toggleModal(null)}
    >
      <div className="modal-close-button" onClick={() => toggleModal(null)}>&#10005;</div>
      <div style={{backgroundImage: `url(${modalRecordImage})`}} className="modal-image-container" />
    </ReactModal>

    <div className="process-wrapper">
      <div className="content">
        <div className="pure-g">
          <div className="l-box pure-u-1 pure-u-md-1 pure-u-lg-1">
            <h1>{title}</h1>
          </div>
          <div className="l-box pure-u-1 pure-u-md-1 pure-u-lg-3-4">
            <p className="process-text">
              {description}
            </p>
          </div>
        </div>
        <Navbar location={location} />
      </div>
    </div>
    <div className="content">
      <div className="pure-g">
        <Filters
          filters={filters}
          filterCallback={filterCallback}
        />
        <Records
          records={records}
          toggleModal={toggleModal}
        />
      </div>
    </div>
  </div>
);

export default Page;



