import React from 'react';

const Filters = ({ filters, filterCallback }) => (
  <div className="l-box pure-u-1 pure-u-md-1-6 pure-u-lg-1-6">

    {filters.filterCategories.map(filterCategory => (

      <div key={filterCategory.name} className="pure-form pure-form-stacked">
        <fieldset>
          <legend>{filterCategory.name}</legend>
          <div className="filter-group">

            {filterCategory.filters.map(filterObj => (
              <label key={filterObj.name} className="pure-checkbox">
                <input
                  type="checkbox"
                  onClick={() =>
                    filterCallback(filterCategory.name, filterObj.name, filterCategory.code, filterCategory.columnType)}
                />
                &nbsp;{filterObj.name}
              </label>
            ))}

          </div>
        </fieldset>

      </div>

    ))}

  </div>
);

export default Filters;



